import React, { FC, useState } from 'react'
import { View, Text } from '@tarojs/components'
import { AtIcon } from 'taro-ui'
import classnames from 'classnames'
import './index.less'

interface CardProps {
  title?: string,//
  required?: boolean,//
  height?: number,//
  footer?,
  children?,
  right?: boolean,
  isAll?: boolean, // 
  selectAll?: () => void, // 
}

export interface RightProps {
  boxStyle?: React.CSSProperties,
  text?: string,
  iconName?: string,
  iconStyle?: React.CSSProperties,
  textStyle?: React.CSSProperties,
}

/** */
const Card: FC<CardProps> = (props: CardProps) => {
  let [more, setMore] = useState(false)
  return (
    <View className='card'>
      {
        props.title &&
        <View className='cardTopBox'>
          <View className='cardTop'>{props.title} {props.required && <Text className="required">()</Text>}</View>
          {
            props.right && <View style={{ color: props.isAll ? '#F2544E' : '#999999' }} onClick={props.selectAll}>
              <Text className={`iconfont iconxuanzeyixuan`}></Text>
              <Text className="text"></Text>
            </View>
          }
        </View>
      }
      <View className='cardBody' style={{ 'marginTop': props.title ? '15px' : '0px' }}>
        <View className={classnames({
          heightAuto: more,
          cardView: true
        })} style={{ height: props.height + 'px' || 'auto' }}>
          {props.children}
        </View>
        {props.height && <View className="cardMore" onClick={() => { setMore(!more) }}>{more ? '' : ''}<AtIcon size={18} value={more ? 'chevron-up' : 'chevron-down'} className='icon' /></View>}
      </View>
      {props.footer && <View className='cardFooter'>{props.footer}</View>}
    </View>

  )
}
export default Card
